// imports
@import '../utils/index';
// variables
$form-container-bg-color: #fff;
$submit-btn-container-color: #118b7e;
$submit-btn-color: #fff;
.form-container {
    position: relative;
}

.auth-form-body {
    padding: 35px 20px 0 20px;

    p {
        position: relative;
    }

    .form-group {
        position: relative;
        margin-bottom: 20px;
    }
}

.input-block {
    @include font-size(11px);
    border: 0;
    border-bottom: 1px solid rgba(255, 255, 255, .2);
    background: none;
    color: rgba(255, 255, 255, .5);
    display: block;
    padding: 10px 0;
    text-align: center;
    width: 100%;

    &:focus {
        border-bottom: solid 1px #969696
    }

    &.has-success {
        border-bottom: 1px solid #539665;
    }

    &.has-error {
        border-bottom: 1px solid #d76666;
    }
}

.error-message {
    @include font-size(11px);
    display: inline-block;
    position: absolute;
    background: rgba(215, 102, 102, 0.9);
    padding: 4px;
    z-index: 2;
    color: #FFF;
    border-radius: .25em;
    pointer-events: none;
    visibility: visible;
    opacity: 1;
    top: -15px;

    &:after {
        content: '';
        position: absolute;
        left: 22px;
        bottom: -5px;
        height: 0;
        width: 0;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 5px solid rgba(215, 102, 102, 0.9);
    }
}

.alert-danger,
.alert-success,
.alert-info {
  @include font-size(11px);
  text-align: center;
}

