} */

.forgot-pass {
    @include font-size(11px);
    color: rgba(255, 255, 255, .5);

    &:hover {
        text-decoration: underline;
    }
}

.help-btns-wrapper {
    @include clearfix;
    margin-bottom: 20px;
}

.login-button {
    @extend .btn-block;
    @extend .btn-lg;
    @extend .btn-info;
    @include font-size(14px);
    background-color: #0c6b95;
    border: 0;
    border-radius: 2px;
    color: $submit-btn-color;
    cursor: pointer;
    margin-bottom: 15px;
    outline: none;
    padding: 10px 0;
    text-align: center;
    width: 100%;
}

.social-btns {
    @include clearfix();
    display: inline-block;
    margin-left: 90px;
    vertical-align: middle;
    float: right;
    ;
}

.facebook-ico {
    background: url('../images/icons/social/l-facebook.svg');
    background-size: cover;
    display: block;
    float: left;
    height: 20px;
    width: 20px;
}

.google-ico {
    background: url('../images/icons/social/l-google.svg');
    background-size: cover;
    display: block;
    float: left;
    height: 20px;
    margin-left: 10px;
    width: 20px;
}

.dha {
    @include font-size(11px);
    color: rgba(255, 255, 255, .5);
    text-align: center;
    a {
        color: rgba(255, 255, 255, .8);
    }
}

.register-button {
    @extend .login-button;
}

.register-with {
    @include font-size(11px);
    color: rgba(255, 255, 255, .5);
    float: left;
    line-height: 20px;
}
